import React, { useState } from 'react';
import { Icon, Button } from 'react-native-elements'
import { StyleSheet, Text, View, SafeAreaView, FlatList, TextInput } from 'react-native';

export default function ChatPage(props) {
    const { } = props;
    const [text, setText] = useState(['']);
    const [messages, setMessages] = useState([
        { user: 'Temo', message: 'Hi!', others: true, },
        { user: 'Lisa', message: 'Hi, I\'m at the meeting location', others: true, },
        { user: 'Xinyang', message: 'Hello everyone!', others: true, },
        { user: 'Andreas', message: 'Be there in a min!', others: true, },
    ]);
    return (

        <SafeAreaView style={styles.safeArea}>
            <h2 style={{padding: 8}}>Your Commute Buddies</h2>
            <View style={styles.card}>
                <View style={styles.card_header_container}>
                    <Text style={styles.card_caption}>Your Columbia Team:</Text>

                </View>
                <View style={styles.card_header_container} marginTop={8}>
                    <FlatList
                        horizontal={true}
                        data={[
                            { key: 'Temo', like: true, },
                            { key: 'Lisa', like: true, },
                            { key: 'Xinyang', like: true, },
                            { key: 'Andreas', like: true, },
                        ]}
                        renderItem={({ item }) => <Button
                            type='clear'
                            icon={<View alignItems='center'>
                                <Icon
                                    name="user"
                                    type='font-awesome'
                                    size={80}
                                    color='#B9D9EB'
                                    marginHorizontal={16}
                                />
                                <Text style={styles.name_text}>{item.key}</Text>
                            </View>}
                        />
                        }
                    />
                </View>
            </View>
            <View style={styles.chatContainer}>
                <View style={styles.input}><TextInput
                    value={text}
                    style={{ height: 40 }}
                    placeholder="Type your message here"
                    onChangeText={t => setText(t)}

                    onSubmitEditing={(t) => {
                        messages.push({ user: 'User', message: text, others: false, })
                        setText('');
                    }}
                /></View>
                <FlatList
                    horizontal={false}
                    data={messages}
                    renderItem={({ item, index }) => <View style={styles.card_item}>
                        <View style={item.others ? styles.messageContainer : styles.messageContainerReverse}><Text>{item.user}</Text></View>
                        <View style={item.others ? styles.messageContainer : styles.messageContainerReverse}><Text style={item.others ? styles.card : styles.cardReverse}>{item.message}</Text></View>
                    </View>
                    }
                />
                <View style={{ flex: 100 }}></View>
            </View>
        </SafeAreaView>
    );
}

const styles = StyleSheet.create({
    safeArea: {
        flex: 1,
    },
    chatContainer: {
        flex: 1,
        flexDirection: 'column-reverse',
        alignSelf: 'stretch',
        padding: 8,
        marginHorizontal: 8,
        marginTop: 8,
        marginBottom: 8,
        borderStyle: 'solid',
        borderWidth: 1,
        borderRadius: 4,
        borderColor: '#afafaf'
    },
    messageList: {
        flex: 1,
    },
    messageContainer: {
        flexDirection: 'row',
    },
    messageContainerReverse: {
        flexDirection: 'row-reverse',
    },
    card: {
        alignSelf: 'stretch',
        alignItems: 'stretch',
        padding: 8,
        marginHorizontal: 8,
        marginTop: 8,
        borderStyle: 'solid',
        borderWidth: 1,
        borderRadius: 4,
        borderColor: '#afafaf'
    },
    cardReverse: {
        alignSelf: 'stretch',
        alignItems: 'stretch',
        padding: 8,
        marginHorizontal: 8,
        marginTop: 8,
        borderStyle: 'solid',
        borderWidth: 1,
        borderRadius: 4,
        borderColor: '#afafaf',
        backgroundColor: '#B9D9EB',
    },
    input: {
        alignSelf: 'stretch',
        alignItems: 'stretch',
        padding: 8,
        marginHorizontal: 0,
        marginTop: 8,
        borderStyle: 'solid',
        borderWidth: 1,
        borderRadius: 4,
        borderColor: '#000D74'
    },
    card_item: {
        marginBottom: 16,
    },
    card_header_container: {
        alignSelf: 'stretch',
        alignItems: 'center',
        flexDirection: 'row',
        justifyContent: 'space-between',
    },
    card_caption: {
        fontSize: 10,
        fontWeight: '400',
        letterSpacing: 0.25,
        color: '#000D74',
    },
    name_text: {
        color: '#000D74',
        textAlign: 'center',
    },
});
